import Vue from 'vue'
import Router from 'vue-router'
import Into from '@/components/Into'
import Out from '@/components/Out'
import Income from '@/components/Income'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/into'
    },
    {
      path: '/into',
      name: 'Into',
      component: Into
    },
    {
      path: '/out',
      name: 'Out',
      component: Out
    },
    {
      path: '/income',
      name: 'Income',
      component: Income
    }
  ]
})



